import { connect } from 'react-redux';
import { DEMO_ANALYTICS, getOverViewData, refreshAnalytics, updateDateFilter, updateFlags } from '../../../../actions/analytics';
import Conversion from './Conversion';
import Website from './Website';
import Shopon from './Shopon';
import ContentGallery from './ContentGallery';
import { GET_WEBSITE_LIST } from '../../../../actions/websiteAction';
import { GET_ALL_SHOPON_LIST } from '../../../../actions/shoponAction';
import './analytics.scss'
import { Loader } from '../CommonComponents/Loader';
import withRouter from '../../../routes/WithRouter';
import { GET_WALL_ID, GET_WEBSITE_LIST_ID, TOGGLE_SHOPPABLE_MODAL } from '../../../../reducer/keys';
import ShoppableModal from '../CommonComponents/ShoppableModal';
import Breadcrumbs from '../CommonComponents/Breadcrumb';
import { shopifyApp } from '../../../../utils';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
import OverlayLoader from '../CommonComponents/OverlayLoader';


class Analytics extends Component {
    state = {
        startDate: moment().subtract(30, 'days').format("DD-MM-YYYY"),
        endDate: moment().format("DD-MM-YYYY"),
        maxDate: moment().endOf('month').format("DD-MM-YYYY"),
        overViewWall: [0],
        overName: 'All',
        websiteWall: [],
        webName: 'All',
        shopOnWall: '0',
        shopName: 'All',
        activeTab: '1',
        isDisabled: false,
        selectedTab: 'Overview',
        selectedWebList: [],
        selectedWallList: [],
        generatePDFLoader: false,
    }

    componentDidMount() {
        const { websiteList, overViewList } = this.props;
        // this.setState({ selectedWebList: websiteList?.map(i => ({ name: i.wall?.ThemeInfo?.wallName, id: i.wall?.ThemeInfo?.webId, wallId: i?.wall?.ThemeInfo?.wallId })) })
        if (overViewList?.length) this.setState({ selectedWallList: overViewList }, () => {
            this.handleWallChange(overViewList, true);
            this.props.addWallId(overViewList?.map(i => i.id));
            this.props.getOverView([0], this.state.activeTab, [], '', true)
        })
    }

    componentDidUpdate(prevProps, prevState) {
        const { websiteList, overViewList, wallsLoader, homeData, getOverView } = this.props;
        const { activeTab } = this.state;
        const hash = window.location.hash.substring(1);

        // Check wallsLoader status
        if (prevProps.wallsLoader !== wallsLoader && !wallsLoader) {
            if (!(homeData?.allfeedsCount && homeData?.allfeedsCount > 0)) {
                this.props.DEMO_ANALYTICS();
            } else if (hash) {
                this.changeTab(hash == "overview" ? 1 : hash == "content" ? 2 : hash == "conversion" ? 3 : hash == "website" ? 4 : hash == "shopon" ? 5 : 1)
            }
        }

        // if(prevProps.websiteList?.length !== websiteList?.length) this.setState({ selectedWebList: websiteList.map(i => ({ name: i.wall?.ThemeInfo?.wallName, id: i.wall?.ThemeInfo?.webId, wallId: i?.wall?.ThemeInfo?.wallId })) })

        if (prevProps.overViewList?.length !== overViewList?.length) this.setState({ selectedWallList: overViewList }, () => {
            this.handleWallChange(overViewList, true)
            this.props.addWallId(overViewList?.map(i => i.id));
            this.props.getOverView([0], activeTab, [], '', true)
        })
        if (prevProps.websiteList?.length !== websiteList?.length) this.handleWallChange(overViewList, true)
    }

    componentWillMount() {
        const { overViewWall, activeTab } = this.state
        const { websiteList, shopOnList, wallsLoader, homeData, webList, shopList, getOverView, flags, getFlags, overViewList } = this.props
        const hash = window.location.hash.substring(1);
        if (!flags.length) getFlags();
        if (!wallsLoader && !(homeData?.allfeedsCount > 0)) {
            this.props.DEMO_ANALYTICS();
            this.setState({ isDisabled: true })
        }
        else if (hash) {
            this.changeTab(hash == "overview" ? 1 : hash == "content" ? 2 : hash == "conversion" ? 3 : hash == "website" ? 4 : hash == "shopon" ? 5 : 1)
        }
        webList();
        if (!shopOnList?.length && hash == "shopon") shopList()
    }

    changeTab = (value) => {
        const { isConversion, isOverview, isGallery, isShopOn, isWebsite, navigate, websiteList, shopOnList, webList, shopList, analyticsData } = this.props
        const { overViewWall, shopOnWall, websiteWall } = this.state
        const { wall_id, website_list_id } = analyticsData
        navigate(`/analytics#${value == 1 ? 'overview' : value == 2 ? 'content' : value == 3 ? 'conversion' : value == 4 ? 'website' : value == 5 ? 'shopon' : "overview"}`)
        if (value == 4 && !websiteList?.length) webList()
        else if (value == 5 && !shopOnList?.length) shopList()
        this.setState({ activeTab: value })
        if ((value == 1 && isOverview) || (value == 2 && isGallery) || (value == 3 && isConversion) || (value == 4 && isWebsite) || (value == 5 && isShopOn)) {
            this.props.getOverView(overViewWall, value, websiteWall, shopOnWall)
        }
    }

    componentWillUnmount() {
        this.props.refresh()
    }

    changeWall = (item, type) => {
        const { activeTab, shopOnWall, websiteWall, overViewWall } = this.state
        const { getOverView, analyticsData } = this.props
        const { wall_id, website_list_id } = analyticsData

        if (activeTab == 4) {
            getOverView(overViewWall, activeTab, type ? 0 : item?.wall.ThemeInfo?.webId, shopOnWall)
            this.setState({ websiteWall: type ? 0 : item?.wall.ThemeInfo?.webId, webName: type ? 'All' : item?.wall?.ThemeInfo?.wallName })
        } else if (activeTab == 5) {
            getOverView(overViewWall, activeTab, websiteWall, type ? 0 : item.id)
            this.setState({ shopOnWall: type ? 0 : item.id, shopName: type ? 'All' : item.name })
        } else {
            getOverView(overViewWall, activeTab, websiteWall, shopOnWall)
            this.setState({ overViewWall: type ? 0 : item.id, overName: type ? 'All' : item.name })
        }
    }

    handleOnApply = (event, picker) => {
        this.setState({ startDate: picker.startDate.format("DD-MM-YYYY"), endDate: picker.endDate.format("DD-MM-YYYY") })
    }

    handleCallback = (start, end) => {
        this.props.updateDateFilter({ startDate: start, endDate: end }, this.state.overViewWall, this.state.websiteWall, this.state.shopOnWall)
        this.setState({ startDate: start.format("DD-MM-YYYY"), endDate: end.format("DD-MM-YYYY") })
    };

    changeTabFun = e => { this.setState({ selectedTab: e }) }

    handleWallChange = (item, status) => {
        item?.forEach(element => {
            const res = this.props.websiteList.filter(i => i?.wall?.ThemeInfo?.wallId === element?.id)
            this.filterWebsiteList('selectedWebList', res, status)
        });
    }

    filterWebsiteList = (listName, data, status) => {
        this.setState(prevState => {
            let updatedList = [...(prevState[listName] || [])];
            data?.forEach(item => {
                const webId = item?.wall?.ThemeInfo?.webId;
                const wallName = item?.wall?.ThemeInfo?.wallName;
                const wallId = item?.wall?.ThemeInfo?.wallId;
                if (status) updatedList = [...updatedList, { name: wallName, id: webId, wallId }];
                else updatedList = updatedList.filter(i => i.id !== webId);
                // const existsIndex = updatedList.findIndex(i => i.id === webId);
                // if (existsIndex !== -1) {
                //     updatedList = updatedList.filter(i => i.id !== webId);
                // } else {
                //     updatedList = [...updatedList, { name: wallName, id: webId, wallId }];
                // }
            });
            this.props.addWebsiteListId(updatedList?.map(i => i.id))
            return {
                [listName]: updatedList
            };
        });
    }


    // handleWebListChange = (item) => {
    //     item.forEach(element => {
    //         const res = this.props.overViewList.find(i => i?.id === element.id)
    //         this.filterWallList({ ...res, webId: element.webId })
    //     });
    // }

    // filterWallList = (data) => {
    //     this.setState(prevState => {
    //         let updatedList = [...(prevState.selectedWallList || [])];
    //         const existsIndex = updatedList.findIndex(i => i.id === data.id);

    //         if (existsIndex !== -1) {
    //             const result = this.state.selectedWebList.find(i => i.wallId == data.id)            
    //             if (!result) {
    //                 updatedList = updatedList.filter(item => item.id !== data.id)
    //             }

    //         } else {
    //             updatedList = [...updatedList, { name: data.name, id: data.id }];
    //         }

    //         this.props.addWallId(updatedList?.map(i => i.id))
    //         return {
    //             selectedWallList: updatedList
    //         };
    //     });
    // };

    handleToggleSelect = (listName, item, callback = () => { }) => {
        this.setState(prevState => {
            const list = prevState[listName];
            const exists = list?.find(i => i.id === item.id);
            const updatedList = exists ? list?.filter(i => i.id !== item.id) : [...list, item];
            listName == 'selectedWebList' ? this.props.addWebsiteListId(updatedList?.map(i => i.id)) : this.props.addWallId(updatedList?.map(i => i.id))
            return { [listName]: updatedList };
        }, callback);
    };


    getDisplayText = (status) => {
        if (this.state[status ? 'selectedWebList' : 'selectedWallList']?.length === 0) return status ? 'All Website' : 'All Gallery';
        if (this.state[status ? 'selectedWebList' : 'selectedWallList']?.length === 1) return this.state[status ? 'selectedWebList' : 'selectedWallList'][0].name;
        return `${this.state[status ? 'selectedWebList' : 'selectedWallList']?.length} ${status ? 'Websites' : 'Galleries'}`;
    };

    generatePDF = async () => {

        try {
            this.setState({ generatePDFLoader: true });

            await new Promise(resolve => setTimeout(resolve, 3000)); // buffer delay

            const sections = document.querySelectorAll('.pdf-page');
            const pdf = new jsPDF('p', 'mm', 'a4');

            const fixAvatarImages = (container) => {
                const images = container.querySelectorAll('img');
                images.forEach((img) => {
                    let src = img.getAttribute('src') || '';
                    src = src.replace(/&amp;/g, '&');
                    if (src.includes('ui-avatars.com/')) {
                        img.setAttribute('crossorigin', 'anonymous');
                    }
                    img.setAttribute('src', src);
                });
            };

            const inlineBlockedImages = async (container) => {
                const images = container.querySelectorAll('img');
                for (let img of images) {
                    const src = img.getAttribute('src') || '';
                    if (src.startsWith('http') && !src.startsWith(window.location.origin)) {
                        try {
                            const res = await fetch(src, { mode: 'cors' });
                            const blob = await res.blob();
                            const dataUrl = await new Promise((resolve) => {
                                const reader = new FileReader();
                                reader.onloadend = () => resolve(reader.result);
                                reader.readAsDataURL(blob);
                            });

                            if (dataUrl.startsWith('data:image')) {
                                img.setAttribute('src', dataUrl);
                                img.setAttribute('crossorigin', 'anonymous');
                            } else {
                                console.warn(' Not a valid image base64:', src);
                            }
                        } catch (err) {
                            const cleanUrl = src.replace(/^https?:\/\//, '');
                            const fallback = await toDataURL(`https://s3.us-west-1.wasabisys.com/${cleanUrl}`);
                            img.setAttribute('src', fallback || src);
                        }
                    }
                }
            };

            const toDataURL = async (url) => {
                try {
                    const response = await fetch(url);
                    const blob = await response.blob();
                    const dataUrl = await new Promise((resolve, reject) => {
                        const reader = new FileReader();
                        reader.onloadend = () => resolve(reader.result);
                        reader.onerror = reject;
                        reader.readAsDataURL(blob);
                    });
                    return dataUrl;
                } catch (error) {
                    return url;
                }
            };

            const waitForAssetsToLoad = (container) => {
                const images = container.querySelectorAll('img');
                const imagePromises = Array.from(images).map((img) =>
                    img.complete
                        ? Promise.resolve()
                        : new Promise((resolve) => {
                            img.onload = img.onerror = resolve;
                        })
                );

                const svgWait = new Promise((resolve) => {
                    requestAnimationFrame(() => {
                        setTimeout(resolve, 500);
                    });
                });

                return Promise.all([...imagePromises, svgWait]);
            };

            const expandScrollableContainers = (container) => {
                const scrollables = container.querySelectorAll('[style*="overflow"], .scrollable');
                const originalStyles = [];

                scrollables.forEach((el) => {
                    originalStyles.push({
                        el,
                        originalHeight: el.style.height,
                        originalOverflow: el.style.overflow,
                    });

                    el.style.height = el.scrollHeight + 'px';
                    el.style.overflow = 'visible';
                });

                return () => {
                    originalStyles.forEach(({ el, originalHeight, originalOverflow }) => {
                        el.style.height = originalHeight;
                        el.style.overflow = originalOverflow;
                    });
                };
            };

            for (let i = 0; i < sections.length; i++) {
                const section = sections[i];

                fixAvatarImages(section);
                await inlineBlockedImages(section);
                await waitForAssetsToLoad(section);

                const restoreScroll = expandScrollableContainers(section);

                const canvas = await html2canvas(section, {
                    scale: 1.2,
                    useCORS: true,
                    allowTaint: false,
                    backgroundColor: '#efefef',
                });

                restoreScroll();

                const imgData = canvas.toDataURL('image/png');

                // Validate image type
                let imageType = null;
                if (imgData.startsWith('data:image/png')) {
                    imageType = 'PNG';
                } else if (imgData.startsWith('data:image/jpeg')) {
                    imageType = 'JPEG';
                } else {
                    console.error('Unknown image format. Skipping this page.');
                    continue;
                }

                try {
                    const pageWidth = pdf.internal.pageSize.getWidth();
                    const imgProps = pdf.getImageProperties(imgData);
                    const pdfHeight = (imgProps.height * pageWidth) / imgProps.width;

                    pdf.addImage(imgData, imageType, 0, 0, pageWidth, pdfHeight);

                    if (i < sections.length - 1) {
                        pdf.addPage();
                    }
                } catch (err) {
                    console.error('Error adding image to PDF:', err);
                    continue;
                }
            }

            pdf.save('full-report.pdf');
            this.setState({ generatePDFLoader: false });
        } catch (error) {
            console.error('Error generating PDF:', error);
            this.setState({ generatePDFLoader: false });
        }
    }
    
    render() {
        const { activeTab, startDate, endDate, maxDate, overName, webName, shopName, isDisabled, selectedTab, selectedWebList, selectedWallList,generatePDFLoader } = this.state
        const { overViewList, shopOnList, websiteList, wallsLoader, homeData, userData, closeShoppableModal } = this.props
        const wallList = activeTab == 5 ? shopOnList : overViewList
        const shoppableStatus = userData?.userData?.user_data?.UserRule?.shoppableStatus
        const appContent = window?.APP_CONTENT ?? {};
        const { Analytics: content, sideBar: breadCrumbs } = appContent; 
        const isShopify = shopifyApp();
        return <>
            {generatePDFLoader ? <OverlayLoader /> : null}
            <Tab.Container id="left-tabs-example" activeKey={activeTab} onSelect={event => this.changeTab(event)}>
            {!isShopify ? <div className="sub-crumbs f-between flex-wrap border-bottom h-50px py-0" fluid='true'>
                <div className="d-flex align-items-center flex-wrap">
                    <Breadcrumbs crumbs={[{ name: breadCrumbs?.analytics ?? "", link: "/analytics", icon: "grid-2" }, { name: breadCrumbs?.home ?? "", link: "/home" }, { name: breadCrumbs?.analytics ?? "" }]} crumbIcon={'grid-2'} />
                </div>
            </div> : null}


            <div className="subheader py-1 py-lg-0" style={!isShopify ? { marginTop: '50px' } : {}}>
                <Container fluid>
                    <div className='f-between w-100'>
                        <div className="d-flex align-items-center flex-nowrap flex-shrink-0 me-3">
                            <Nav variant="underline" className='d-none d-lg-flex' activeKey={activeTab}>
                                <Nav.Item>
                                    <Nav.Link eventKey="1">
                                        Overview
                                    </Nav.Link>
                                </Nav.Item>
                                {/* <Nav.Item>
                                        <Nav.Link eventKey="2">
                                            Content Gallery
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="3">
                                            Conversion
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="4">
                                            Website
                                        </Nav.Link>
                                    </Nav.Item> */}
                                {shoppableStatus ? <Nav.Item>
                                    <Nav.Link eventKey="5">
                                        Shopon.bio
                                    </Nav.Link>
                                </Nav.Item> : null}
                            </Nav>
                            <Dropdown className='d-flex d-lg-none'>
                                <Dropdown.Toggle variant="secondary" className="btn-sm" id="analytics-tab">
                                    {selectedTab}
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <NavDropdown.Item onClick={e => this.changeTabFun('Overview')} eventKey="1">Overview</NavDropdown.Item>
                                    {/* <NavDropdown.Item onClick={e => this.changeTabFun('Content Gallery')} eventKey="2">Content Gallery</NavDropdown.Item>
                                        <NavDropdown.Item onClick={e => this.changeTabFun('Conversion')} eventKey="3">Conversion</NavDropdown.Item>
                                        <NavDropdown.Item onClick={e => this.changeTabFun('Website')} eventKey="4">Website</NavDropdown.Item> */}
                                    {shoppableStatus ? <NavDropdown.Item onClick={e => this.changeTabFun('Shopon.bio')} eventKey="5">Shopon.bio</NavDropdown.Item>:null}
                                </Dropdown.Menu>
                            </Dropdown>
                        </div>
                        <div className="d-flex align-items-center flex-nowrap flex-shrink-0">
                            <Dropdown className='me-3 py-1' style={{ pointerEvents: `${isDisabled ? 'none' : ''}` }}>
                                <Dropdown.Toggle variant='secondary' size='sm' id="a_change_con" className='btn-min-w f-between d-flex'>{activeTab == 5 ? shopName : this.getDisplayText(false)}</Dropdown.Toggle>
                                <Dropdown.Menu style={{ maxHeight: '300px', overflowY: 'auto' }}>
                                    {wallList && wallList.map((item) => {
                                        const name = item.name;
                                        const id = item.id;
                                        const isChecked = selectedWallList?.some(i => i.id === item.id);
                                        return (
                                            <Dropdown.Item as="button" key={id} onClick={(e) => {
                                                e.stopPropagation();
                                                
                                                this.handleToggleSelect("selectedWallList", { id, name }, () => {
                                                    this.handleWallChange([item], !isChecked);
                                                    this.changeWall(item);
                                                });
                                            }}>
                                                <Form.Check
                                                    type="checkbox"
                                                    label={name}
                                                    checked={isChecked}
                                                    readOnly
                                                />
                                            </Dropdown.Item>
                                        );
                                    })}
                                </Dropdown.Menu>
                            </Dropdown>
                            {
                                activeTab == 1 ?
                                    <Dropdown className='me-3 py-1' style={{ pointerEvents: isDisabled ? 'none' : '' }}>
                                        <Dropdown.Toggle variant='secondary' size='sm' id="multi_select_dropdown" className='btn-min-w f-between d-flex' disabled={selectedWebList?.length > 0 ? false : true }>
                                            {this.getDisplayText(true)}
                                        </Dropdown.Toggle>
                                        <Dropdown.Menu style={{ maxHeight: '300px', overflowY: 'auto' }}>
                                            {selectedWebList?.map((item, index) => {
                                                const id = item?.id
                                                const name = item?.name
                                                const isChecked = selectedWebList.some(i => i.id === id);
                                                return (
                                                    <Dropdown.Item as="button" key={id} onClick={(e) => {
                                                        e.stopPropagation();
                                                        this.handleToggleSelect("selectedWebList", { id, name, wallId: item?.wallId }, () => {  this.changeWall(item)});
                                                    }}>
                                                        <Form.Check type="checkbox" label={name} checked={isChecked} readOnly />
                                                    </Dropdown.Item>
                                                );
                                            })}
                                        </Dropdown.Menu>
                                    </Dropdown> : null
                            }

                            <div className='py-1' style={{ pointerEvents: `${isDisabled ? 'none' : ''}` }}>
                                <DateRangePicker onApply={this.handleOnApply} startDate={startDate} endDate={endDate} initialSettings={{
                                    locale: { format: 'DD-MM-YYYY', separator: '-' },
                                    maxDate: maxDate,
                                    showDropdowns: true,
                                    startDate: startDate, endDate: endDate,
                                    ranges: {
                                        Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                                        Yesterday: [moment().subtract(1, 'days').format("DD-MM-YYYY"), moment().subtract(1, 'days').format("DD-MM-YYYY"),],
                                        'Last 7 Days': [moment().subtract(6, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                                        'Last 30 Days': [moment().subtract(30, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                                        'Last Month': [moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"), moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),],
                                    },
                                }} onCallback={this.handleCallback}
                                >
                                    <Button variant='secondary' aria-label='calendar' size='sm' id="date_range">
                                        <FontAwesomeIcon icon={'calendar-range'} iconClass={'me-1'} />
                                        {`${startDate} - ${endDate}`}
                                    </Button>
                                </DateRangePicker>

                            </div>

                            {activeTab == 1 ? <button type="button" aria-label="Mode" className={`btn btn-secondary btn-sm ms-3 ${generatePDFLoader ? 'disabled' : ''}`} onClick={this.generatePDF}>
                                {
                                    generatePDFLoader ? <span className="spinner-border spinner-border-sm align-middle ms-2 disabled"></span> : <FontAwesomeIcon icon={'download'} iconClass={'fa-duotone fa-solid me-0'} />
                                }
                            </button> : null}
                        </div>
                    </div>
                </Container>
            </div>
            <div className='d-flex flex-column flex-column-fluid full-content' style={!isShopify ? { paddingTop: '70px' } : {}}>
                {!wallsLoader ? <div id="analytics_main" className="w-100 analytics_">
                    {!(homeData?.allfeedsCount > 0) ?
                        <Container fluid>
                            {/* <Alert className='bg-primary p-2 rounded-1 text-gray-100 fw-medium text-center'> */}
                            <Alert className='custom-alert'>
                                {content?.demo_msg ?? ""}
                            </Alert>
                        </Container>
                        : null}

                    <Tab.Content>
                        <Tab.Pane eventKey="1">{activeTab == '1' ? <Overview {...{generatePDFLoader, content }} /> : null}</Tab.Pane>
                        {/* <Tab.Pane eventKey="2">{activeTab == '2' ? <ContentGallery /> : null}</Tab.Pane>
                        <Tab.Pane eventKey="3">{activeTab == '3' ? <Conversion /> : null}</Tab.Pane>
                        <Tab.Pane eventKey="4">{activeTab == '4' ? <Website /> : null}</Tab.Pane> */}
                        <Tab.Pane eventKey="5">{activeTab == '5' && shoppableStatus ? <Shopon content={content?.shopon_tab ?? {}} /> : null}</Tab.Pane>
                    </Tab.Content>
                </div>
                    :
                    <div className='h-100' style={{ minHeight: 'calc(100vh - 200px)' }}><Loader bg="body" /></div>}
            </div>
            {userData?.showShoppableModal ? <ShoppableModal userData={userData} allWalls={overViewList} hideModal={closeShoppableModal} /> : null}
        </Tab.Container>
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        isOverview: state.analyticsOverView?.counts_loader,
        isGallery: state.analyticsContentGallery?.type_by_post_network_loader,
        isConversion: state.analyticsConversion?.conversion_loader,
        isWebsite: state.analyticsWebsite?.websiteOverview_loader,
        isShopOn: state.analyticsShopOn?.shopOnOverview_loader,
        websiteList: state.websiteList?.websiteList,
        shopOnList: state.shopOnList?.shopOnList,
        overViewList: state.dashboardData?.dashboarddata?.allWalls,
        wallsLoader: state.dashboardData?.wallsLoader,
        homeData: state.dashboardData?.dashboarddata,
        flags: state.analyticsOverView?.flags,
        userData: state.authorized,
        analyticsData: state.analyticsOverView
    }
}

const mapDispatchToProps = dispatch => {
    return {
        updateDateFilter: (data, wallId, websiteId, shopOnid) => dispatch(updateDateFilter(data, wallId, websiteId, shopOnid)),
        getOverView: (overviewWall, tabId, websiteId, shopOnid, status) => dispatch(getOverViewData(overviewWall, tabId, websiteId, shopOnid, status)),
        webList: () => dispatch(GET_WEBSITE_LIST()),
        shopList: () => dispatch(GET_ALL_SHOPON_LIST()),
        refresh: () => dispatch(refreshAnalytics()),
        DEMO_ANALYTICS: () => dispatch(DEMO_ANALYTICS()),
        addWebsiteListId: (data) => dispatch({ type: GET_WEBSITE_LIST_ID, payload: data }),
        addWallId: (data) => dispatch({ type: GET_WALL_ID, payload: data }),
        getFlags: () => dispatch(updateFlags()),
        closeShoppableModal: () => dispatch({ type: TOGGLE_SHOPPABLE_MODAL, payload: false })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Analytics))